import React from 'react'
import { useState } from 'react';
import { ethers, BigNumber } from 'ethers';
import spyLadies from './SpyLadies.json';
import { Input, Button, Box, Flex, Text } from '@chakra-ui/react';


const spyLadiesAddress = "0x94B5142f8873EE824Ce7dfCFfBeC9638F2b19E2E";

const MainMint = ({accounts, setAccounts}) => {
  const [mintAmount, setMintAmmount] = useState(1);
  const isConnected = Boolean(accounts[0]);
  async function handleMint() {
    if (window.ethereum) {
        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const signer = provider.getSigner();
        const contract = new ethers.Contract(
            spyLadiesAddress,
            spyLadies.abi,
            signer
        );
        try{
            const response = await contract.mint(BigNumber.from(mintAmount), {
              value: ethers.utils.parseEther((0.02*mintAmount).toString()),
            });
            console.log('response: ', response);
        } catch (err) {
            console.log("error: ", err)
        }
    }
  }

  const handleDecrement = () => {
    if(mintAmount <= 1) return;
    setMintAmmount(mintAmount - 1);
  };
  const handleIncrement = () => {
    if(mintAmount >= 3) return;
    setMintAmmount(mintAmount + 1);
  };

  return (
    <Flex justify="center" align="center" height="100vh" paddingBottom ="150px">
      <Box width="560px">
      <Text fontSize="48px" textShadow="0 5px #00 0000">RobotoPunks</Text>
        <Text fontSize="30px"
          letterSpacing="-5.5%"
          fontFamily="VT323"
          textShadow="0 2px 2px #000000">
          It's 3048. Can the RobotoPunks NFT save humans from destructive 
          rampant NFT speculation? Mint RobotoPunksNFT to find out.
        </Text>
        {isConnected ? (
          <div> 
            <Flex align="center" justify="center"> 
              <Button
                backgroundColor = "#D6517D"
                borderRadius="5px"
                boxShadow = "0px 2px 2px 1px #0F0F0F"
                color = "white"
                cursor = "pointer"
                fontFamily = "inherit"
                padding  = "15px"
                margin = "0 15px"
                onClick = {handleDecrement}>
                -
              </Button>
              <Input 
                readOnly
                fontFamily="inherit"
                width="100px"
                height="40px"
                textAlign="center"
                paddingLeft="19px"
                marginTop="10px"
                type="number"
                value = {mintAmount} />
              <Button 
                backgroundColor = "#D6517D"
                borderRadius="5px"
                boxShadow = "0px 2px 2px 1px #0F0F0F"
                color = "white"
                cursor = "pointer"
                fontFamily = "inherit"
                padding  = "15px"
                margin = "0 15px"
                onClick = {handleIncrement}>
                +
                </Button>
            </Flex>
            <Button 
                backgroundColor = "#D6517D"
                borderRadius="5px"
                boxShadow = "0px 2px 2px 1px #0F0F0F"
                color = "white"
                cursor = "pointer"
                fontFamily = "inherit"
                padding  = "15px"
                margin = "0 15px"
                onClick = {handleMint}>
                MINT NOW
                </Button>
          </div>
        ) : (
          <Text
            marginTop="70px"
            fontSize="30px"
            letterSpacing="-5.5%"
            fontFamily="VT323"
            textShadow = "0 3px #000000"
            color = "#D6517D">
            You must be connected to Mint.
          </Text>
        )}
      </Box>
    </Flex>
  )
} 

export default MainMint